<template>
  <div class="like">
    <van-nav-bar
      title="我的收藏"
      left-text="返回"
      left-arrow
      :fixed="true"
      @click-left="back"
    />
    <div class="like-bg"></div>
    <div class="like-box">
      <div class="product-box clearfix" v-if="likeData">
        
        <div class="product-item fl" v-for="(item,index) in likeData" :key="index" :class="[index % 2 == 0 ? 'p-left' : 'p-right']">
          <div class="p-img" @click="viewProductInfo(item)">
            <img :src="item.cover" alt />
          </div>
          <div class="pro-name one-text">{{ item.name }}</div>
          <div class="clearfix">
            <div class="pro-price fl">{{ item.author }}</div>
            <div class="pro-remove fr" @click="removeLike(index)">
              <van-icon name="delete" />
            </div>
          </div>
        </div>
      </div>
      <div v-else>
        <van-empty description="收藏书籍空空如也，去逛一逛!">
          <van-button
            round
            color="#49dda1"
            class="bottom-button"
            @click="goMenu"
          >
            去逛一逛
          </van-button>
        </van-empty>
      </div>
    </div>
  </div>
</template>

<script>
import "../../assets/scss/bookrack.scss";
// import { mhSerchApi } from "@/api/api";
export default {
  data() {
    return {
      //我的收藏商品数据
      likeData: [],
    
    };
  },

  created() {
    this.getAllLikeData();
  },

  methods: {
    goMenu() {
      return this.$router.push({ name: "Classify" });
    },
    back() {
      this.$router.go(-1);
    },

    //获取我的收藏商品数据
    getAllLikeData() {
      //获取token字符串
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return this.$router.push({ name: "Login" });
      }
    
      let to= localStorage.getItem("itemsArray");
      this.likeData  = JSON.parse(to)
     
    
   
    },

    //查看商品详情
 viewProductInfo(){
   
   
   
  //  let url = item.name;
  //   mhSerchApi(url).then((res) => {
  //    let { code} = res.data;
     
  //       if(code == 0 ){
          
  //         let searchinglist = res.data.list;
        
          
  //       }
      
  //   })
 },
    //删除收藏商品数据
    removeLike(index) {
      
     
     this.likeData.splice(index,1)
   
    }
  },
};
</script>

